@import url(../molecules/product-card.css);

.product-card-list {
	margin: 0;
	padding: 0;
	list-style: none;
	display: grid;
	gap: 0.5rem;
	grid-template-columns: repeat(var(--product-card-list-column-count), 1fr);
}

@media (width < 728px) {
	.product-card-list {
		--product-card-list-column-count: 2;
	}
}

@media (728px <= width < 1024px) {
	.product-card-list {
		--product-card-list-column-count: 3;
	}
}

@media (width >= 1024px) {
	.product-card-list {
		--product-card-list-column-count: 6;
	}
}

.product-card-list__item {
	min-width: 0;
}

.product-card-list__product-card {
	height: 100%;
}
