@import url(../atoms/heading.css);
@import url(../molecules/catalog-nav-button.css);

@media (width >= 1024px) {
	.catalog-nav {
		--catalog-nav-column-gap: 1rem;

		display: grid;
		grid-template-columns: 1fr 1fr;
		column-gap: var(--catalog-nav-column-gap);
		position: relative;
	}
}

.catalog-nav__list {
	margin: 0;
	padding: 0;
	list-style: none;
}

.catalog-nav__list--nested:not(.catalog-nav__list--open) {
	display: none;
}


@media (width < 1024px) {
	.catalog-nav__list--nested {
		padding-left: 2rem;
	}
}

@media (width >= 1024px) {
	.catalog-nav__list--nested {
		position: absolute;
		width: calc(50% - var(--catalog-nav-column-gap) / 2);
		top: 0;
		left: calc(50% + var(--catalog-nav-column-gap) / 2);
	}
}

.catalog-nav__item:not(.catalog-nav__list--nested *) {
	border-bottom: 0.125rem solid currentColor;
}

.catalog-nav__link {
	text-decoration: none;
	color: inherit;
	font-size: 1rem;
	line-height: 1.5rem;
	padding: 0.125rem 0;
}

@media (width < 1024px) {
	.catalog-nav__link {
		display: inline-block;
	}
}

@media (width >= 1024px) {
	.catalog-nav__link {
		display: block;
	}
}
