:root {
	--background-image-gradient-primary: linear-gradient(360deg, #0E0E0E 54.08%, #2E1F2B 78.47%);

	--color-light-primary: #FFF;
	--color-light-secondary: #E4E4E4;
	--color-light-tertiary: #808080;

	--color-dark-primary: #262626;
	--color-dark-secondary: #181818;

	--color-pink-primary: #872371;

	--font-family-default: sans-serif;
	--font-family-primary: 'Roboto', var(--font-family-default);

	--border-radius-primary: 0.5rem;
	--border-radius-secondary: 2rem;
}

@media (width < 1024px) {
	:root {
		--header-height: 3rem;
	}
}

@media (width < 1024px) {
	:root {
		--nav-height: 3rem;
	}
}

@media (width >= 1024px) {
	:root {
		--header-height: 4rem;
	}
}
