@import url(../molecules/form.css);
@import url(../molecules/socials.css);

.footer {
	color: var(--color-light-primary);
	background-color: var(--color-dark-primary);
}

.footer__body {
	padding: 1rem 0;
	display: flex;
	flex-direction: column;
	row-gap: 1rem;
}

.footer__form > .button {
	color: var(--color-dark-primary);
}

.footer__text {
	font-family: var(--font-family-primary);
	font-size: 0.875rem;
	line-height: 1.5rem;
	color: var(--color-light-tertiary);
}
