.heading {
	margin: 0;
	font-family: var(--font-family-primary);
}

@media (width < 728px) {
	.heading {
		font-size: 1.5rem;
		line-height: 2rem;
	}
}

@media (728px <= width < 1024px) {
	.heading {
		font-size: 2rem;
		line-height: 2.5rem;
	}
}

@media (1024px <= width < 1440px) {
	.heading {
		font-size: 2.5rem;
		line-height: 3rem;
	}
}

@media (width >= 1440px) {
	.heading {
		font-size: 3rem;
		line-height: 3.5rem;
	}
}

@media (width < 728px) {
	.heading--secondary {
		font-size: 1.25rem;
		line-height: 2rem;
	}
}

@media (728px <= width < 1024px) {
	.heading--secondary {
		font-size: 1.5rem;
		line-height: 2rem;
	}
}

@media (1024px <= width < 1440px) {
	.heading--secondary {
		font-size: 2rem;
		line-height: 2.5rem;
	}
}

@media (width >= 1440px) {
	.heading--secondary {
		font-size: 2.5rem;
		line-height: 3rem;
	}
}

.heading--tertiary {
	font-size: 1rem;
	line-height: 1.5rem;
}

.heading--mb-primary {
	margin-bottom: 1rem;
}

.heading--mb-secondary {
	margin-bottom: 0.5rem;
}

.heading--center {
	text-align: center;
}
