.catalog-nav-button {
	margin: 0;
	padding: 1rem 0;
	background-color: transparent;
	border: none;
	color: inherit;
	width: 100%;
	text-align: start;
	display: flex;
	align-items: center;
	column-gap: 0.5rem;
}

.catalog-nav-button {
	font-size: 1.5rem;
	line-height: 2rem;
}

.catalog-nav-button__img {
	width: 1.5rem;
	height: 1.5rem;
	flex-shrink: 0;
}

.catalog-nav-button:after {
	content: '';
	width: 1.5rem;
	height: 1.5rem;
	mask-image: url(../../img/angle-right.svg);
	mask-position: center;
	mask-size: contain;
	mask-repeat: no-repeat;
	background-color: currentColor;
	flex-shrink: 0;
	margin-left: auto;
	transition: transform 64ms linear;
}

.catalog-nav-button[aria-expanded='true']:after {
	transform: rotateZ(90deg);
}
