.socials {
	display: flex;
	flex-direction: column;
	row-gap: 0.5rem;
}

.socials__list {
	margin: 0;
	padding: 0;
	list-style: none;
	display: flex;
	column-gap: 0.5rem;
}

.socials__link {
	color: inherit;
	display: inline-flex;
	align-items: center;
}

.socials__img {
	fill: currentColor;
	width: 1.5rem;
	height: 1.5rem;
}
