@import url(../atoms/container.css);
@import url(../molecules/logo.css);
@import url(../molecules/catalog-button.css);
@import url(../molecules/search.css);
@import url(../molecules/nav.css);

.header {
	background-color: var(--color-light-secondary);
}

.header__body {
	height: var(--header-height);
	display: flex;
}

@media (width < 1024px) {
	.header__body {
		justify-content: space-between;
	}
}

@media (width >= 1024px) {
	.header__logo {
		margin-right: 2rem;
	}
}

@media (width >= 1024px) {
	.header__catalog-button {
		margin-right: 4rem;
	}
}

@media (width < 1024px) {
	.header__search {
		display: none;
	}
}

@media (width >= 1024px) {
	.header__search {
		width: 18rem;
	}
}

@media (width < 1024px) {
	.header__nav {
		width: 100%;
		position: fixed;
		left: 0;
		bottom: 0;
	}
}

@media (width >= 1024px) {
	.header__nav {
		margin-left: auto;
	}
}
