@media (width < 1024px) {
	.nav {
		color: var(--color-light-primary);
		background-color: var(--color-dark-secondary);
	}
}

@media (width >= 1024px) {
	.nav {
		display: flex;
		column-gap: 2rem;
	}
}

.nav__list {
	margin: 0;
	padding: 0;
	list-style: none;
	display: flex;
}

@media (width < 1024px) {
	.nav__list {
		justify-content: space-between;
		height: var(--nav-height);
	}
}

@media (width >= 1024px) {
	.nav__list {
		height: 100%;
	}
}

@media (width < 1024px) {
	.nav__list--secondary {
		display: none;
	}
}

.nav__link {
	text-decoration: none;
	font-family: var(--font-family-primary);
	padding: 0 0.5rem;
	height: 100%;
	display: flex;
	align-items: center;
	line-height: 1.5rem;
	font-weight: 700;
	color: inherit;
}

@media (width < 1024px) {
	.nav__link {
		font-size: 0.875rem;
	}
}

@media (width >= 1024px) {
	.nav__link {
		font-size: 1rem;
	}
}

.nav__link--decor {
	flex-direction: column;
	justify-content: center;
}

.nav__link--decor:before {
	content: '';
	background-color: currentColor;
	mask-size: contain;
	mask-repeat: no-repeat;
}

@media (width < 1024px) {
	.nav__link--decor:before {
		width: 1.25rem;
		height: 1.25rem;
	}
}

@media (width >= 1024px) {
	.nav__link--decor:before {
		width: 1.5rem;
		height: 1.5rem;
	}
}

.nav__link--cart:before {
	mask-image: url(../../img/cart.svg);
}

.nav__link--user:before {
	mask-image: url(../../img/user.svg);
}
