.page {
	background-image: var(--background-image-gradient-primary);
	scroll-behavior: smooth;
	scroll-padding-top: var(--header-height);
}

@media (width < 1024px) {
	.page {
		padding-bottom: var(--nav-height);
	}
}

.page__body {
	margin: 0;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
}

.page__header {
	position: fixed;
	width: 100%;
	top: 0;
	z-index: 10;
}

.page__catalog {
	position: fixed;
	width: 100%;
	top: var(--header-height);
	z-index: 10;
}

.page__main {
	display: flex;
	flex-direction: column;
	row-gap: 2rem;
	margin-bottom: 1rem;
	flex-grow: 1;
}

.page__main > section:first-child {
	min-height: calc(100vh - var(--header-height));
	padding-top: calc(var(--header-height) + var(--page-main-first-child-padding-top));
}

@media (width < 728px) {
	.page__main {
		--page-main-first-child-padding-top: 1rem;
	}
}

@media (728px <= width < 1024px) {
	.page__main {
		--page-main-first-child-padding-top: 4rem;
	}
}

@media (1024px <= width < 1440px) {
	.page__main {
		--page-main-first-child-padding-top: 6rem;
	}
}

@media (width >= 1440px) {
	.page__main {
		--page-main-first-child-padding-top: 7rem;
	}
}
