.catalog-button {
	margin: 0;
	padding: 0;
	border: none;
	background-color: transparent;
	display: flex;
	align-items: center;
	cursor: pointer;
}

@media (width >= 1024px) {
	.catalog-button {
		font-family: var(--font-family-primary);
		font-size: 1rem;
		line-height: 1.5rem;
		font-weight: 700;
		column-gap: 0.5rem;
	}
}


.catalog-button__img {
	width: 1.5rem;
	height: 1.5rem;
	stroke: var(--color-dark-primary);
}

.catalog-button[aria-expanded='true'] .catalog-button__img--list {
	display: none;
}

.catalog-button[aria-expanded='false'] .catalog-button__img--cross {
	display: none;
}

@media (width < 1024px) {
	.catalog-button__text {
		display: none;
	}
}
