@import url(../atoms/heading.css);

.product-card {
	padding: 0.5rem;
	box-sizing: border-box;
	font-family: var(--font-family-primary);
	text-decoration: none;
	color: inherit;
	position: relative;
	background-color: var(--color-dark-primary);
	border-radius: var(--border-radius-primary);
	display: flex;
	flex-direction: column;
	row-gap: 0.5rem;
}

.product-card__discount {
	padding: 0 0.5rem;
	font-size: 1rem;
	line-height: 1.5rem;
	font-weight: 700;
	background-color: var(--color-light-primary);
	color: var(--color-dark-primary);
	border-radius: var(--border-radius-primary);
	position: absolute;
	top: 0.5rem;
	left: 0.5rem;
}

.product-card__img {
	object-fit: contain;

}

@media (width < 728px) {
	.product-card__img {
		height: 12rem;
	}
}

@media (width >= 728px) {
	.product-card__img {
		height: 14rem;
	}
}

.product-card__heading {
	overflow: hidden;
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
	margin-bottom: 0.5rem;
}

.product-card__total-price {
	font-size: 1.5rem;
	line-height: 2rem;
}

.product-card__price {
	font-size: 1rem;
	line-height: 1.5rem;
	color: var(--color-light-tertiary);
	text-decoration: line-through;
}
