.button {
	margin: 0;
	padding: 0.5rem 2rem;
	border: none;
	font-size: 1rem;
	line-height: 1.5rem;
	font-weight: 700;
	background-color: var(--color-pink-primary);
	border-radius: var(--border-radius-secondary);
	color: inherit;
	cursor: pointer;
}

.button--secondary {
	padding: 0 0.5rem;
	background-color: var(--color-light-primary);
	border-radius: var(--border-radius-primary);
}
